.header-container {
    height: 80px;
    border-bottom: 1px solid #00000017;
    position: fixed;
    color: var(--text-color-primary);
    top: 0;
    left: 0;
    right: 0;
    min-height: 80px;
    padding: 0 var(--page-pad-small);
    transition: all 0.6s ease-in-out;
    z-index: 10;
}

.intro {
    position: relative;
    z-index: 1;
}

.logo, .logo-image {
    height: 26px;
    padding-right: 8px;
    cursor: pointer;
    text-decoration: none;
    letter-spacing: 3px;
    font-size: 14px;
    color: var(--text-color-primary);
}

@media only screen and (min-width: 48em) {
    .header-container {
        padding: 0 var(--page-pad-medium);
    }
}

.desk-nav {
    margin-left: 20px;
    display: none;
}

.desk-nav li {
    display: inline-block;
    position: relative;
}

.desk-nav li label {
    font-weight: 600;
    font-size: 15px;
    padding: 12px 26px;
    cursor: pointer;
}

.desk-nav li label:hover {
    background-color: var(--bg-color-hover);
    border-radius: var(--border-radius);
}

.desk-nav li ul {
    position: absolute;
    padding: 12px 0;
    min-width: 172px;
    top: 50px;
    left: 0;
    background-color: #fff;
    border-radius: var(--border-radius);
    color: var(--text-color-primary);
    border: 1px solid var(--border-color-primary);
    min-width: 210px;
}

.desk-nav li ul li {
    padding: 8px 18px;
    min-width: 174px;
    cursor: pointer;
}

.desk-nav li ul li:hover {
    background-color: var(--bg-color-hover-light);
}

.desk-nav li ul li svg {
    width: 28px;
}

.nav-menus:not(.active) {
    display: none;
}

.scrolled.header-container {
    background-color: var(--bg-glass-color-header);
    border-bottom: 1px solid var(--border-glass-color-header);
}

@media only screen and (min-width: 78rem) {
    .desk-nav {
        display: block;
    }
    .header-container {
        padding: 0 var(--page-pad-large);
    }
}