:root {
    --bg-glass-color-header: #ffffffe3;
    --border-glass-color-header: #92002f61;
    --page-pad-large: 100px;
    --page-pad-medium: 70px;
    --page-pad-small: 20px;

    --content-pad-large: 60px;
    --content-pad-medium: 70px;
    --content-pad-small: 20px;
    --content-pad-xsmall: 12px;

    --text-color-primary: #000;
    --text-color-invert: #fff;
    --text-color-brand: #92002f;
    --border-color-primary: #000;
    --border-color-neutral: #000;
    --border-color-brand: #87002b;
    --bg-color: #fff;
    --bg-color-brand: #92002f;
    --bg-color-hover: #f0f0f0;
    --bg-color-hover-light: #f0f0f0;
    --bg-color-hover-brand: #92002ecc;
    --border-radius: 8px;
    --border-radius-input: 4px;

    --text-title-size-large: 60px;
    --text-title-lineheight-size-large: 68px;
    --text-subtitle-size-large: 32px;
    --text-p-size-large: 18px;

    --text-title-size-medium: 48px;
    --text-title-lineheight-size-medium: 60px;
    --text-subtitle-size-medium: 24px;
    --text-p-size-medium: 16px;

    --text-title-size-small: 30px;
    --text-title-lineheight-size-small: 38px;
    --text-subtitle-size-small: 20px;
    --text-p-size-small: 14px;
}

section.content {
    width: 100%;
    margin: 0 auto;
    padding: 0 var(--page-pad-small);
}

label {
    display: block;
    padding-bottom: 3px;
}
input, textarea, select {
	height: 32px;
	border-radius: var(--border-radius-input);
    border: 1px solid  rgb(221, 219, 218);
    padding: 0 14px;
    width: 100%;
}

textarea {
    height: unset;
    padding: 14px;
}

textarea:focus, input:focus, select:focus {
    outline: none;
}

@media only screen and (min-width: 48em) {
    section.content {
        padding: 0 var(--page-pad-medium);
    }
}

@media only screen and (min-width: 78rem) {
    section.content {
        padding: 0 var(--page-pad-large);
    }
}

.flex {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}

.flex-sb {
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.flex-align-center {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.flex-justify-center {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.flex-direction-row {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
}
.flex-direction-column {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}
.flex-wrap {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap
}
.jf-hide {
    display: none;
}

.jf-p-left_xsmall {
    padding-left: 8px;
}

.jf-p-left_small {
    padding-left: 12px;
}

.jf-p-right_small {
    padding-right: 12px;
}

.jf-p-vertical_small {
    padding-top: 12px;
    padding-bottom: 12px;
}
.jf-p-vertical_medium {
    padding-top: 16px;
    padding-bottom: 16px;
}
.jf-p-vertical_large {
    padding-top: 24px;
    padding-bottom: 24px;
}

.jf-m-vertical_small {
    margin-top: 12px;
    margin-bottom: 12px;
}
.jf-m-vertical_medium {
    margin-top: 16px;
    margin-bottom: 16px;
}
.jf-m-vertical_large {
    margin-top: 24px;
    margin-bottom: 24px;
}

.jf-morphism-brand {
    background: rgba(146, 0, 47, 0.6);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
}

.jf-morphism-white {
    /* From https://css.glass */
    background: rgba(255, 255, 255, 0.6);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
    border: 1px solid rgba(255, 255, 255, 0.2);
}