.banner {
    display: block;
    position: relative;
    color: var(--text-color-primary);
    width: 100%;
    height: 100vh;
    padding: 0 var(--page-pad-small);
    padding-top: 100px !important;
    background-image: url('../assets/images/building.jpg');
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
}

.intro, .hero {
    position: relative;
    width: 100%;
    max-width: 100%;
    height: 50%;
}
.intro-greet {
    width: 100%;
    position: absolute;
    top: 36%;
    transform: translateY(-36%);
}
.intro .text-brand {
    color: var(--text-color-brand);
}
.intro .title {
    font-size: var(--text-title-size-small);
    line-height: var(--text-title-lineheight-size-small);
    font-weight: 900;
}
.intro .role {
    font-size: var(--text-subtitle-size-small);
    font-weight: 100;
    margin: 8px 0;
}
.intro .bio {
    font-size: var(--text-p-size-large);
}
.btn-about {
    max-width: 150px;
}

.hero-image {
    background-image: url('../assets/images/self.png');
    background-size: contain;
    background-position: center bottom;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}
.hero-image img {
    width: 80%;
}

@media only screen and (max-height: 512px) {
    .banner {
        margin-top: 100px;
        height: calc(100vh + 500px);
    }
}

@media only screen and (min-width: 48em) {
    .intro {
        max-width: 40%;
    }
    .hero {
        position: absolute;
        top: 0;
        left: 0;
        padding-right: 30px;
    }
    .intro-greet {
        top: 20%;
        transform: translateY(-20%);
    }
    .intro, .hero {
        height: 100%;
    }
    .banner {
        padding: 0 var(--page-pad-medium);
        background-size: contain;
    }
    .intro .title {
        font-size: var(--text-title-size-medium);
        line-height: var(--text-title-lineheight-size-medium);
        font-weight: 900;
    }
    .intro .role {
        font-size: var(--text-subtitle-size-medium);
        font-weight: 100;
        margin: 8px 0;
    }
    .intro .bio {
        font-size: var(--text-p-size-medium);
    }
    .hero-image {
        background-position: right bottom;
        width: 70%;
        margin-left: auto;
    }
}

@media only screen and (min-width: 78rem) {
    .banner {
        padding: 0 var(--page-pad-large);
    }
    .intro-greet {
        top: 36%;
        transform: translateY(-36%);
    }
    .intro .title {
        font-size: var(--text-title-size-large);
        line-height: var(--text-title-lineheight-size-large);
        font-weight: 900;
    }
    .intro .role {
        font-size: var(--text-subtitle-size-large);
        font-weight: 100;
        margin: 8px 0;
    }
    .intro .bio {
        font-size: var(--text-p-size-large);
    }
}