body {
	margin: 0;
	font-size: 16px;
	background-color: #fff;
	color: #000;
	font-family: "Salesforce sans", sans-serif, "Segoe UI", "Roboto";
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
html, body, section, #root, .app {
    height: 100%;
}
* {
	box-sizing: border-box;
}
p {
	margin: 0;
}
ul {
	padding-left: 0;
}
li {
	list-style: none;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.coming-soon {
	padding-top: 160px;
	color: var(--text-color-brand);
	font-weight: 500;
	font-size: 20px;
	text-align: center;
	width: 100%;
	margin-left: -18px;
}