
section.page-contact {
    padding-top: 80px;
    font-size: 18px;
}
input, textarea, select {
    background:  rgba(255, 255, 255, 0.2);
}
.form-container {
    width: 100%;
    padding: 40px 20px;
    margin: 40px auto;
    font-size: 16px;
    max-width: 960px;
}
.title {
    width: 40%;
    display: none;
}
.form {
    width: 100%;
}
.instruction {
    font-weight: 400;
    font-size: 14px;
    padding-bottom: 20px;
    border-bottom: 1px solid var(--border-color-primary);
}
section.page-contact h1 {
    font-size: 28px;
    font-weight: 500;
}
.instruction > div:first-child {
    width: 50%;
}
.instruction > div:last-child {
    text-align: right;
}
.avatar {
    text-align: center;
    width: 80%;
    padding-left: 20px;
}
.avatar img {
    width: 100%;
}
.img-avatar-mob {
    max-height: 60px;
    display: block;
}
.btn-contact {
    max-width: 100%;
}
@media only screen and (max-width: 480px) {
    .instruction {
        flex-direction: column-reverse;
    }
    .instruction > div:first-child {
        width: 100%;
        padding-top: var(--content-pad-small);
    }
}
@media only screen and (min-width: 48em) {
    section.content {
        padding: 0 var(--page-pad-medium);
    }
    .title {
        display: block;
    }
    .form {
        width: 60%;
    }
    .img-avatar-mob {
        display: none;
    }
    .form-container {
        padding: 60px 40px;
    }
    .btn-contact {
        max-width: 100px;
        margin-left: auto;
    }
}